import { assetGroupsUrl } from "../../constatns/urls";
import { getUrlService } from "../../services/getUrl.service";
import { setAssetGroups, updateAssetGroups } from "../../store";

export const getAssetGroups = async ({ brand, env }) => {
  updateAssetGroups({ loading: true });
  try {
    const response = await fetch(
      getUrlService({ url: assetGroupsUrl, brand, env })
    );
    const { data } = await response.json();
    setAssetGroups({ data });
  } catch (error) {
    updateAssetGroups({ error: true });
  }
  updateAssetGroups({ loading: false });
};
