import { writable } from "svelte/store";
import { filterSymbolsByAssetGroup } from "./services/symbols.services";
import { mapAssetGroupsDataFromServer } from "./services/assetGroups.services";

const storeState = {
  options: {},
  activeAssetGroup: {},
  activeSymbol: {},
  assetGroups: {
    visibleGroups: [],
    allGroups: [],
    loading: false,
    error: "",
  },
  symbols: {
    visibleSymbols: [],
    allSymbols: [],
    loading: false,
    error: "",
  },
};
const mainStore = writable(storeState);

const { update } = mainStore;

const setAssetGroups = ({ data }) => {
  update((store) => {
    const newData = mapAssetGroupsDataFromServer({
      data,
      options: store.options,
    });
    return {
      ...store,
      activeAssetGroup: newData[0],
      assetGroups: {
        ...store.assetGroups,
        visibleGroups: newData,
        allGroups: data,
      },
    };
  });
};

const updateAssetGroups = (assetGroups) => {
  update((store) => ({
    ...store,
    assetGroups: { ...store.assetGroups, ...assetGroups },
  }));
};

const updateSymbols = (symbols) => {
  update((store) => ({
    ...store,
    symbols: { ...store.symbols, ...symbols },
  }));
};

const setSymbols = (activeAssetGroup) => {
  update((store) => {
    const newData = filterSymbolsByAssetGroup({
      allSymbols: store.symbols.allSymbols,
      options: store.options,
      activeAssetGroup,
    });
    return {
      ...store,
      // activeSymbol: newData[0],
      symbols: { ...store.symbols, visibleSymbols: newData },
    };
  });
};

const updateOptions = (options) => {
  update((store) => ({ ...store, options }));
};

const setActiveAssetGroup = (activeAssetGroup) => {
  update((store) => ({ ...store, activeAssetGroup, activeSymbol: {} }));
};

const setActiveSymbol = (activeSymbol) => {
  update((store) => ({ ...store, activeSymbol }));
};

export {
  mainStore,
  setActiveSymbol,
  setSymbols,
  updateOptions,
  updateSymbols,
  setAssetGroups,
  updateAssetGroups,
  setActiveAssetGroup,
};
