export const numberWithDigits = (number, digits) => number.toFixed(digits);

export const getTrendSymbol = (trend) => trend[0] - trend[trend.length - 1] > 0;
export const getPercentage = (trend) => {
  const a = trend[0];
  const b = trend[trend.length - 1];
  if (a > b) {
    return (((a - b) / b) * 100).toFixed(2) + "%";
  } else {
    return "-" + (((b - a) / b) * 100).toFixed(2) + "%";
  }
};
