import { symbolsUrl } from "../../constatns/urls";
import { setSymbols, updateSymbols } from "../../store";
import { getUrlService } from "../../services/getUrl.service";

export const getSymbols = async ({ brand, env }) => {
  updateSymbols({ loading: true });
  try {
    const response = await fetch(
      getUrlService({ url: symbolsUrl, brand, env })
    );
    const { data } = await response.json();
    updateSymbols({ allSymbols: data });
  } catch (error) {
    updateSymbols({ error: true });
  }
  updateSymbols({ loading: false });
};
